@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700|Josefin+Sans:400,600,700,400italic,600italic);



.Converter {
    width: 100%;
    padding: 0 15%;
    box-sizing: border-box;
    text-align: center;
  }.Form {
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }.Form input,
  select {
    padding: 5px;
    margin: 5px 10px;
    border-radius: 5px;
    border: 1px solid rgba(119, 119, 119, 0.5);
  }.Form button {
    padding: 5px 10px;
    border-radius: 5px;
    background: rgb(11, 170, 59);
    color: white;
    border: none;
  }.Converter h3 {
    background-color: #ccc;
    border-radius: 10px;
    padding: 15px;
    font-size: 30px;
  }.Converter h2 span {
    color: rgb(11, 170, 59);
  }
.header {
  width: 100%;
  height: auto;
  display: flex;
  background-color: rgb(78, 72, 63);
  padding: 0 50px;
}

.header-logo {
  display: flex;
  height: 100px;
}

.footer {
  background-color: rgb(78, 72, 63);
  display: flex;
  flex-shrink: 0;
  width: 100%;
  height: 300px;
  left: 0;
  bottom: 0;
  align-items: center;
  padding: 0 50px;
  justify-content: space-between;
}

.footer-brand {
  text-align: justify;
  color: #fff;
  font-size: 12px;
  width: 400px;
  height: auto;
}

.footer-logo {
  display: flex;
  justify-self: flex-end;
}

.carousel {
  flex: 1 1;
  text-align: center;
  background: #364d79;
  position: relative;
}

.Thomas {
  position: relative;
  font-family: Helvetica;
  background: #f0f2f5;
}

.contact {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px 0;
}

.map-img {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.transition-img {
  width: 100%;
  height: auto;
  -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, color-stop(10%, transparent), color-stop(90%, #f0f2f5));
  -webkit-mask-image: linear-gradient(to bottom, transparent 10%, #f0f2f5 90%);
  mask-image: -webkit-gradient(linear, left top, left bottom, color-stop(10%, transparent), color-stop(90%, #f0f2f5));
  mask-image: linear-gradient(to bottom, transparent 10%, #f0f2f5 90%);
}

.down-arrow-image{
  width: 20px;
  height: auto;
}


body{
	font-family: 'josefin sans';
	font-size: 16px;
}
/* https://images.unsplash.com/photo-1439902315629-cd882022cea0?fit=crop&fm=jpg&h=975&q=80&w=1925 */
.hero{
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 9;
	-webkit-transition: all 1.6s cubic-bezier(0.86, 0, 0.07, 1);
	transition: all 1.6s cubic-bezier(0.86, 0, 0.07, 1);
}
.hero.scrolled{
	-webkit-transform: translate3d(0, -100%, 0) scale(.75);
	        transform: translate3d(0, -100%, 0) scale(.75);
	opacity: 0;
}
.hero-inner{
	background-image: url('https://files.hipcdn.com/photos/thailand/5599801a70726f1f360003e0/5d89977ca12eda0c54000533/walden-thonglor-8-condo-watthana-5dc0f129a12eda20990137ed_full.jpg');
	background-size: cover;
	background-position: center;
	display: table;
	width: 100%;
	height: 100vh;
	top: 0;
}
.hero-title{
	display: table-cell;
	vertical-align: middle;	
	text-align: center;
}
h1, h2, h3, h4, h5, h6{
	font-family: montserrat;
}
.font-2{
	font-family: 'josefin sans';
	font-weight: 700;
}
.title{ letter-spacing: .3em; text-transform: uppercase; }
.text-light{ color: #fff }
.font-alt{
	font-family: 'georgia';
	font-style: italic;
	color: #666;
}

.hero{
	overflow: hidden;
	z-index: 1;
}
.content{
	position: relative;
	background-color: #fff;
	border-top: 10px solid black;
	padding: 0;	
	margin: 0;
	-webkit-transition: all 2.5s cubic-bezier(0.86, 0, 0.07, 1);
	transition: all 2.5s cubic-bezier(0.86, 0, 0.07, 1);
	-webkit-transform: translate3d(0, 10px, 0) scale(.35);
	        transform: translate3d(0, 10px, 0) scale(.35);
	opacity: 0;
}
.content.scrolled{
	-webkit-transform: translate3d(0, 0, 0) scale(1);
	        transform: translate3d(0, 0, 0) scale(1);	
	opacity: 1;
	position: relative;
}
.sd{
	color: black;
	position: absolute;
	bottom: 20px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}
.sd:hover, .sd:focus{
	color: #fff;
	opacity: .7;
	text-decoration: none;
}
/* work in progrece */
@media only screen and (max-width: 1200px) {
	.hero-inner{
	
			background-size:100% auto;
		
	}
}
